<template>
	<div class="moto-one">
		<div class="wc-content">
			</div>
		<div class="wc-content-r">
			<div class="cont-0">
				<span class="cont-1">系统查询</span>
				<span>/</span>
				<span class="cont-2">说明说明说明说明</span>
			</div>
			<div class="cont-r">
				<el-button :plain="true" type="success">EXCEL导出</el-button>
			</div>
			<div class="box">
				<div class="type">
				<div class="bix">
					<el-button type="primary" class="tj">添加管理员</el-button>
					<span>点击后显示二维码，微信扫描加入</span>
				</div>	
	<el-table
    :data="tableData4"
    border
    style="width: 100%"
    max-height="250">
    <el-table-column
      fixed
      prop="date"
      label="日期"
      width="150">
    </el-table-column>
    <el-table-column
      prop="name"
      label=""
      width="140">
	  <template scope="scope">
		<img :src="scope.row.name" alt="">
	  </template>
    </el-table-column>
    <el-table-column
      prop="province"
      label="管理员"
      width="140">
    </el-table-column>
    <el-table-column
      prop="city"
      label="手机号"
      width="140">
    </el-table-column>
    <el-table-column
      prop="address"
      label="加入时间"
      width="300">
    </el-table-column>
    <el-table-column
      prop="zip"
      label="权限角色"
      width="140">
    </el-table-column>
    <el-table-column
      fixed="right"
      label="操作"
      width="140">
      <template scope="scope">
        <el-button
          @click.native.prevent="deleteRow(scope.$index, tableData4)"
          type="text"
          size="small">
          移除
        </el-button>
		<el-button
          @click.native.prevent="deleteRow(scope.$index, tableData4)"
          type="text"
          size="small">
          权限
        </el-button>
      </template>
    </el-table-column>
  </el-table>


		</div>
		</div>
		</div>
	</div>	
</template>
<script>
	export default {
		components:{
		},
		methods: {
      deleteRow(index, rows) {
        rows.splice(index, 1);
      }
    },
    data() {
      return {
        tableData4: [{
          date: '2016-05-03',
          name: '../../../static/tu2.png',
          province: '梅志',
          city: '13895467552',
          address: '2016-11-20 16:30:35',
		  zip: '扫厕所大叔',
		  
        }, {
          date: '2016-05-02',
          name: '../../../static/tu2.png',
          province: '王征',
          city: '13895467552',
          address: '2016-11-20 16:30:35',
          zip: '老总',
        }]
      }
    }
	}
</script>
<style lang="scss" scoped>
.moto-one{
	.wc-content-r{
				float: left;
				width: 1399px;
				height: 998px;
				background: #FAFBFF;
				position: relative;
				.cont-0{
					float: left;
					margin-left: 20px;
					.cont-1{
						line-height:90px ;
						font-size: 21px;
						color: #2C4FAB;
						font-weight: bold;
						margin-right:10px;
					}
					span{
						font-size:21px;
						color: #8F9EBA;
						margin:10px;
					}
					.cont-2{
						font-size: 14px;
						color: #8F9EBA;
						/*font-weight: bold;*/
					}
				}
				.cont-r{
					float: right;
					margin-right: 30px;
					margin-top: 38px;
					
				}
				.wc-content{
					float: left;
					width: 1340px;
					border: 1px solid #E8E6F3;
					background: #FFFFFF;
					position: absolute;
					top: 90px;
					left: 20px;
				}
				&:after{
					content: '';
					display: block;
					clear: both;
				}
				.box{
				width: 1340px;
				height:909px;
				border: 1px solid #e7e7f1;
				background: #fff;
				margin:0 auto;
				margin-top: 90px;
				.type{
					width: 1280px;
					margin:0 auto;
				.bix{
					height:90px;
					.tj{
						width: 100px;
						height:40px;
						background: #2f73ff;
						margin-top: 30px;
						
					}
					span{
						font-size: 11px;
						color: #94a0ae;
						line-height:90px;
						margin-left:10px;
					}
				  }
				}
			}
			}
			&:after{
				content: '';
				display: block;
				clear: both;
			}
			
}
</style>


