<template>
    <div class="mz-wd clearfix">
        <div class="mz-wd-img">
            <img src="../../assets/ioo.png" alt=""/>
        </div>
        <div class="mz-wd-nav">
            <ul class="clearfix">
                <router-link tag="li" active-class="active" to="/Syindex"><span>首页</span></router-link>
                <router-link tag="li" active-class="active" to="/Motorcade"><span>我的车队</span></router-link>
                <router-link tag="li" active-class="active" to="/Mymoto8/mooto"><span>我的运单</span></router-link>
                <router-link tag="li" active-class="active" to="/Mymoto9/fukuans"><span>业务记录</span></router-link>
                <router-link tag="li" active-class="active" to="/Header/Motorcade4/Wz_yg"><span>系统设置</span></router-link>
                <router-link tag="li" active-class="active" to="/Naviga/Motorcade3/Tixian"><span>企业中心</span></router-link>
            </ul>
        </div>
        <div class="mz-wd-dl">
            <span>登录</span>
            <span>注册</span>
        </div>
        <div class="">
        	<router-view></router-view>
        </div>
    </div>
</template>
<script>
export default {

}
</script>
<style lang="less" scoped>
*{
    margin: 0;
    padding: 0;
}
.mz-wd{
    width: 1600px;
    height: 80px;
    border-bottom: 1px solid #e6e9f2;
    .mz-wd-img{
        height: 80px;
        width: 200px;
        text-align: center;
        float: left;
        img{
            width: 130px;
            height: 80px;
        }
    }
    .mz-wd-nav{
        width: 100px;
        height: 80px;
        line-height: 80px;
        float: left;
        ul{
            width: 1300px;
            height: 60px;
            li{
                float: left;
                padding: 0 30px;
                font-size: 20px;

                span{
                    display: inline-block;
                    height: 76px;
                }
                &.active{
                    span{
                        color: #3052ac;
                        cursor:pointer;
                        border-bottom: 4px solid #3052ac;
                    }

                }
            }
        }
    }
    .mz-wd-dl{
        width: 100px;
        height: 80px;
        line-height: 80px;
        float: right;
        span{
            margin-left: 10px;
            cursor:pointer;
        }
    }
}
.clearfix:after{
    clear: both;
    content: '';
    display: block;
}
</style>



