<template>
	<div class="">
		<el-button type="success" size="small">确认付款</el-button>
		<el-dialog title="运费支付" :visible.sync="dialogTableVisible">
		  <el-table :data="gridData">
		    <el-table-column property="date"></el-table-column>
		    <el-table-column property="name"></el-table-column>
		    <el-table-column property="address" class="hua">
		    </el-table-column>
		  </el-table>
		  <div slot="footer" class="dialog-footer">
		    <el-button @click="dialogTableVisible = false">取 消</el-button>
		    <el-button type="primary" @click="dialogTableVisible = false">确 定</el-button>
		  </div>
		</el-dialog>
    </div>
</template>
<script>
export default {
    data() {
      return {
        gridData: [{
          date: '支付方式',
          name: '保理支付',
          address: '可用额度:1200000.00元'
        }, {
          date: '',
          name: '余额支付',
          address: '可用余额:1200.00元'
        },{
          date: '',
          name: '本次支付运费合计1200元',
        },{
          date: '输入方式',
          name: '',
          address:''
        }
        ],
        dialogTableVisible: false,
        dialogFormVisible: false,
        form: {
          name: '',
          region: '',
          date1: '',
          date2: '',
          delivery: false,
          type: [],
          resource: '',
          desc: ''
        },
        formLabelWidth: '120px'
      };
    }
};
</script>
<style lang="scss" scoped>
	.hua{
		color: red;
	}
</style>



