<template>
	<div class="moto-one">
		<div class="wc-content">
			</div>
		<div class="wc-content-r">
			<div class="cont-0">
				<span class="cont-1">系统查询</span>
				<span>/</span>
				<span class="cont-2">说明说明说明说明</span>
			</div>
			<div class="cont-r">
				<el-button :plain="true" type="success">EXCEL导出</el-button>
			</div>
			<div class="box">
				<div class="type">
				<div class="bix">
					<el-button type="primary" class="tj">新增权限角色</el-button>	
				</div>	
    <el-table
    :data="tableData"
    border
    style="width: 100%">
    <el-table-column
      prop="date"
      label="权限描述"
      width="320">
    </el-table-column>
    <el-table-column
      prop="name"
      label="角色名称"
      width="320">
    </el-table-column>
    <el-table-column
      prop="address"
      label="用户列表"
      width="320">
    </el-table-column>
    <el-table-column
      fixed="right"
      label="操作"
      width="320">
      <template scope="scope">
        <el-button
          @click.native.prevent="deleteRow(scope.$index, tableData)"
          type="text"
          size="small">
          移除    |
        </el-button>
		<el-button
          @click.native.prevent="deleteRow(scope.$index, tableData)"
          type="text"
          size="small">
          权限设置  |
        </el-button>
        <el-button
          @click.native.prevent="deleteRow(scope.$index, tableData)"
          type="text"
          size="small">
          修改
        </el-button>
      </template>
    </el-table-column>
  </el-table>

		</div>
		</div>
		</div>
	</div>	
</template>
<script>
	export default {
		components:{
		},
		methods: {
      deleteRow(index, rows) {
        rows.splice(index, 1);
      }
    },
     data() {
      return {
        tableData: [{
          date: '超级管理员',
          name: '享有全部权限',
          address: '王征，梅志，王文博'
        }, {
          date: 'CEO',
          name: '享有全部权限',
          address: '王征'
        }, {
          date: '超级管理员',
          name: '享有全部权限',
          address: '王征，梅志，王文博'
        }]
      }
    }

	}
</script>
<style lang="scss" scoped>
.moto-one{
	.wc-content-r{
				float: left;
				width: 1399px;
				height: 998px;
				background: #FAFBFF;
				position: relative;
				.cont-0{
					float: left;
					margin-left: 20px;
					.cont-1{
						line-height:90px ;
						font-size: 21px;
						color: #2C4FAB;
						font-weight: bold;
						margin-right:10px;
					}
					span{
						font-size:21px;
						color: #8F9EBA;
						margin:10px;
					}
					.cont-2{
						font-size: 14px;
						color: #8F9EBA;
						/*font-weight: bold;*/
					}
				}
				.cont-r{
					float: right;
					margin-right: 30px;
					margin-top: 38px;
					
				}
				.wc-content{
					float: left;
					width: 1340px;
					border: 1px solid #E8E6F3;
					background: #FFFFFF;
					position: absolute;
					top: 90px;
					left: 20px;
				}
				&:after{
					content: '';
					display: block;
					clear: both;
				}
				.box{
				width: 1340px;
				height:909px;
				border: 1px solid #e7e7f1;
				background: #fff;
				margin:0 auto;
				margin-top: 90px;
				.type{
					width: 1280px;
					margin:0 auto;
				.bix{
					height:90px;
					.tj{
						width: 110px;
						height:40px;
						background: #2f73ff;
						margin-top: 30px;
						
					}
					
				  }
				}
			}
			}
			&:after{
				content: '';
				display: block;
				clear: both;
			}
			
}
</style>


