<template>
	<div class="h-header">
		<div class="h-header-top">
		<el-table ref="multipleTable" :data="tableData3" border tooltip-effect="dark" @selection-change="handleSelectionChange">
		    <el-table-column type="selection" width="55"></el-table-column>
		    <el-table-column label="车辆信息" width="408">
		      <template scope="scope">{{ scope.row.date }}</template>
		    </el-table-column>
		    <el-table-column label="司机信息" width="408">
		      <template scope="scope">{{ scope.row.name }}<br/>{{scope.row.phone}}</template>
		    </el-table-column>
		    <el-table-column label="定位信息" width="408">
		      <template scope="scope">{{ scope.row.address }}<br/>{{scope.row.dataa}}</template>
		    </el-table-column>
  		</el-table>
  <div style="margin-top: 20px">
    <el-button @click="toggleSelection([tableData3[1], tableData3[2]])" class="bt">请输入要分享的企业代码或公司名称</el-button>
    <el-button @click="toggleSelection()" class="btn1">确认分享</el-button>
  </div>
	</div>
 </div>
</template>
<script>
  export default {
    data() {
      return {
        tableData3: [{
          date: '泸DG5060 厢式车 9.6米 10吨 30方',
          name: '衣龙魁',
          phone:13121366644,
          address: '山东省烟台市莱阳市G204(龙门西路)',
          dataa:'2017-8-8'
        }, {
          date: '泸DG5060 厢式车 9.6米 10吨 30方',
          name: '衣龙魁',
          phone:13121366644,
          address: '山东省烟台市莱阳市G204(龙门西路)',
          dataa:'2017-8-8'
        }, {
          date: '泸DG5060 厢式车 9.6米 10吨 30方',
          name: '衣龙魁',
          phone:13121366644,
          address: '山东省烟台市莱阳市G204(龙门西路)',
          dataa:'2017-8-8'
          
        }],
        multipleSelection: []
      }
    },

    methods: {
      toggleSelection(rows) {
        if (rows) {
          rows.forEach(row => {
            this.$refs.multipleTable.toggleRowSelection(row);
          });
        } else {
          this.$refs.multipleTable.clearSelection();
        }
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      }
    }
  }
</script>
<style scoped>
	.bt{
		background:#F5F6FB;
		color: darkgray;
	}
	.btn1{
		background: #0C9B0B;
		color: #fff;
	}
	.el-table th>.cell {
		height: 40px;
		color: blue;
	}
	.el-table td{
	
		height: 100px;
	}
	.h-header{
		width: 1278px;
		height: 500px;
		padding: 30px;
	}
	.h-header-top{
		width: 1280px;
	}
</style>


