<template>
		  <div class="h-content">
		  	   <div class="h-content-top">
		  	   	  <ul class="clearfix">
		  	   	  	<li>
		  	   	  		<span>司机姓名 </span>
		  	   	  	  <el-input  size="large" placeholder="姓名、车牌号、手机号"  class="int"></el-input>
		  	   	  	</li>
		  	   	  	<li>
		  	   	  		<span>车牌号 </span>
		  	   	  	  <el-input  size="large" placeholder="选择"  class="int"></el-input>
		  	   	  	</li>
		  	   	  	<li>
		  	   	  		<span>手机号</span>
		  	   	  	  <el-input  size="large" placeholder="选择区间" class="int"></el-input>
		  	   	  	</li>
		  	   	  </ul>
		  	   	  <ul class="clearfix">
		  	   	  	<li>
		  	   	  		 <span>车型</span>
							  		<el-input placeholder="姓名、车牌号、手机号" class="int" v-model="input6">
											  <el-select v-model="select" slot="append" placeholder="请选择">
											      <el-option label="1" value="1"></el-option>
											      <el-option label="2" value="2"></el-option>
											      <el-option label="3" value="3"></el-option>
											  </el-select>
									  </el-input>
		  	   	    </li>
		  	   	    <li>
		  	   	  		 <span>吨位</span>
							  		<el-input placeholder="选择" class="int">
											  <el-select  slot="append" placeholder="请选择" v-model="select">
											      <el-option label="1" value="1"></el-option>
											      <el-option label="2" value="2"></el-option>
											      <el-option label="3" value="3"></el-option>
											  </el-select>
									  </el-input>
		  	   	    </li>
		  	   	    <li>
		  	   	  		 <span>车位</span>
							  		<el-input placeholder="选择区间"  class="int">
											  <el-select  slot="append" placeholder="请选择" v-model="select">
											  	  <el-option label="1" value="1"></el-option>
											      <el-option label="2" value="2"></el-option>
											      <el-option label="3" value="3"></el-option>
											  </el-select>
									  </el-input>
		  	   	    </li>
		  	   	  </ul>
		  	   	  <ul class="clearfix">
		  	   	  	 <li>
		  	   	  	 	  <span>溶积</span>
							  		<el-input placeholder="姓名、车牌号、手机号" class="int" v-model="select">
											  <el-select  slot="append" placeholder="请选择">
											  	  <el-option label="1" value="1"></el-option>
											      <el-option label="2" value="2"></el-option>
											      <el-option label="3" value="3"></el-option>
											  </el-select>
									  </el-input>
		  	   	  	 </li>
		  	   	  	 <li>
		  	   	  		 <span>手机号</span>
		  	   	  	   <el-input  size="large" placeholder="选择"  class="int"></el-input>
		  	   	  	</li>
		  	   	  </ul>
		  	   </div>
		  	   <div class="h-content-bottom">
		  	   	      <el-button @click="open" class="bt">保存并邀请</el-button>
		  	   </div>
		  	   <div class="h-footer">
		  	   	  <p>提交后请及时通知司机回复同意定位信息，或请司机用以上手机编辑短信xxx发送到xxx</p>
		  	   	  <p>确认后才可以开启定位等认证服务</p>
		  	   </div>
      </div>
</template>
<script>
export default {
    methods: {
    	 data() {
    return {
      input6: '',
      select: ''
    }
  },
      open() {
        const h = this.$createElement;
        this.$msgbox({
          title: '邀请短信',
          message: h('p', null, [
            h('span', null, ' '),
            h('div', {style: 'background:#F5F6FB;height:180px;color:#76757A;line-height:30px; font-size: 14px;'},'卡友xxxx你好：云运力物流平台承运人大动这有限公司，已把你加入到他的车队信息如果您同意该承运人通过LBS服务对本手机号进行定位，请回复y,不同意不用回复即可。'),
            h('div', {style: 'height:20px; margin-top:20px; font-size: 14px;' }, '说明:'),
            h('div', {style: 'height:20px;font-size: 14px;' }, '1.发送一条短信，将从你的消费账户中扣除0，1元；'),
            h('div', {style: 'height:20px;font-size: 12px;' }, '2.每条短信限57个字，超出部分被将由系统自动转记入下一条。')
          ]),
//        showCancelButton: true,
          confirmButtonText: '发送',
//        cancelButtonText: '取消',
          beforeClose: (action, instance, done) => {
            if (action === 'confirm') {
              instance.confirmButtonLoading = true;
              instance.confirmButtonText = '执行中...';
              setTimeout(() => {
                done();
                setTimeout(() => {
                  instance.confirmButtonLoading = false;
                }, 300);
              }, 3000);
            } else {
              done();
            }
          }
        }).then(action => {
          this.$message({
            type: 'info',
            message: 'action: ' + action
          });
        });
      },
    }
  }
</script>
<style lang="scss" scoped>
	 *{
	 	 margin: 0;
	 	 padding: 0;
	 	 list-style: none;
	 }
	.el-select{
     width:20px;
   }
	.h-content{
	 	  width: 1338px;
	 	  height:500px;
	 	  /*margin-left: 26px;*/
	 	  .h-content-top{
	 	  	  margin-top: 34px;
	 	  	 .clearfix{
		 	  		display: block;
		 	  		content: '';
		 	  		clear: both;
	 	  	 }
	     	  ul{
	     	  	 li{
	     	  	  	float: left;
	     	  	  	margin-left:30px;
	     	  	  	margin-top: 16px;
	      	     span{
	      		     width:100px;
	      		     float: left;
	      		     text-align: center;
	      		     line-height:40px;
	      		     color: #6479AA;
	      		     font-weight:100px;
	      	      }
	            }
	     	   }
	    .int{
	    	 width: 194px;
	    	 float:right;
	    	 /*right:50px;*/
	       }
	     .el-input__inner{
	     	  background:#f5f6fb;
	     }
	   }
	   .moto-one .wc-content-r .wc-content .bt{
	   	padding: 0;
	   }
	   .h-content-bottom{
	   	  width: 1540px;
	   	  margin-top:80px;
	   	  text-align: center;
	   	  margin-left:-200px;
	   	   .bt{
	     	 width: 229px;
	     	 height: 45px;
	     	 background:#0B9B0A;
	     	 border: none;
	     	
	     	 color: white;
	     	 border-radius: 5px;
	     	 div{
	     	 	 margin-top: 30px;
	     	 }
	     	
	      } 
	    }
	   .h-footer{
	   	   width:100%;
	   	   text-align: center;
	   	   margin-left:-130px;
	   	   margin-top: 15px;
	   	   font-size: 14px;
	   	   color: darkgrey;
	   } 
	   .el-input__icon{
	   	  width: 20px;
	   }
	   .el-button--primary{
	   	 background:#0C9B0B;
	   }  
	 }
</style>


