<template lang="html">
 
  <div class="content">
  	<div class="content-wrapper">
			<el-form :model="ruleForm2" :rules="rules2" ref="ruleForm2" label-width="100px" class="demo-ruleForm">
				<h2>承运人注册</h2>
   			<el-row :gutter="20">
					<el-col :span="9" :offset="7">

						<el-form-item
					    label="手机号"
					    prop="age"
					    :rules="[
					      { required: true, message: '手机号不能为空'},
					      { type: 'number', message: '手机号必须为数字值'}
					    ]"
					  >
					    <el-input type="age" v-model.number="ruleForm2.age" auto-complete="off"></el-input>
					  </el-form-item>

						<el-form-item label="密码" prop="pass"
							:rules="{
						     required: true, message: '请输入密码', trigger: 'blur'
						   }"
						>
							<el-input type="password" v-model="ruleForm2.pass" auto-complete="off"></el-input>
						</el-form-item>

						<el-form-item label="确认密码" prop="checkPass"
							:rules="{
						     required: true, message: '请确认密码', trigger: 'blur'
						   }"
						>
							<el-input type="password" v-model="ruleForm2.checkPass" auto-complete="off"></el-input>
						</el-form-item>

					  <el-form-item label="手机验证码" prop='name'>
			          <el-input v-model="ruleForm2.name" auto-complete="off"></el-input>
			      </el-form-item>
					</el-col>
				</el-row>

	   		<h2>企业信息</h2>
   			<el-row :gutter="20">
					<el-col :span="9" :offset="7">

						<el-form-item label="企业名称" prop='name'
					    :rules="{
					     required: true, message: '企业名称不能为空',trigger: 'blur'
					     }"
					  >
					    <el-input v-model="ruleForm2.name" auto-complete="off"></el-input>
					  </el-form-item>

					  <el-form-item label="企业地址" prop="name"
					    :rules="{
					     	required: true, message: '企业地址不能为空',trigger: 'blur'
					     }"
					  >
					    <el-input v-model="ruleForm2.name" auto-complete="off"></el-input>
					  </el-form-item>

						<el-form-item label="负责人" prop="name"
					    :rules="{
					    	required: true, message: '负责人不能为空',trigger: 'blur'
					    	}"
					  >
					    <el-input v-model="ruleForm2.name" auto-complete="off"></el-input>
					  </el-form-item>

						<el-form-item label="联系手机" prop="name"
					    :rules="{ required: true, message: '手机号不能为空',trigger: 'blur'}"
					  >
					    <el-input v-model="ruleForm2.name" auto-complete="off"></el-input>
					  </el-form-item>

						<el-form-item label="传真" prop="name">
					    <el-input v-model="ruleForm2.name" auto-complete="off"></el-input>
					  </el-form-item>

					  <el-form-item
					    label="运营证书"
					    prop="name"
					    :rules="{
					    	required: true, message: '运营证书不能为空'
					    }"
					  >
					    <el-input v-model="ruleForm2.name" auto-complete="off"></el-input>
					  </el-form-item>

					  <el-form-item label="营业执照" prop="name"
					    :rules="{
					    	required: true, message: '营业执照不能为空'
					    }"
					  >
					    <el-input type="name" v-model="ruleForm2.name" auto-complete="off"></el-input>
					  </el-form-item>

					  <el-form-item
					    label="其他资质证书"
					    prop="name">
					    <el-input v-model="ruleForm2.name" auto-complete="off"></el-input>
					  </el-form-item>
            <el-form-item>
              <el-checkbox v-model="checked">阅读并同意
                <span><i class="el-icon-d-arrow-left"></i>软件许可及服务协议<i class="el-icon-d-arrow-right"></i></span></el-checkbox></span>
              </el-form-item>
					</el-col>
				</el-row>

				<el-row :gutter="20">
					<el-col :span="7" :offset="9">

					</el-col>
				</el-row>
			</el-form>
       <el-button type="danger"  @click="open"><router-link to="/Syindex">注册</router-link></el-button>
       <router-view></router-view>
	  </div>
  </div>
</template>

<script>
export default {
  data() {
    var checkAge = (rule, value, callback) => {
      if (!value) {
        return callback(new Error('手机号不能为空'));
      }
    };
    var validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'));
      } else {
        if (this.ruleForm2.checkPass !== '') {
          this.$refs.ruleForm2.validateField('checkPass');
        }
        callback();
      }
    };
    var validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请再次输入密码'));
      } else if (value !== this.ruleForm2.pass) {
        callback(new Error('两次输入密码不一致!'));
      } else {
        callback();
      }
    };
    return {
      input: '',
      checked: true,
      ruleForm2: {
        pass: '',
        checkPass: '',
        age: '',
        name: '',
        rules2: '',
        delivery: false,
        type: [],
        resource: '',
        desc: ''
      },
      rules2: {
        pass: [{
          validator: validatePass,
          trigger: 'blur'
        }],
        checkPass: [{
          validator: validatePass2,
          trigger: 'blur'
        }],
        age: [{
          validator: checkAge,
          trigger: 'blur'
        }]
      }
    };
  },
   components:{
	 
		
      
			
		
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
