  width: 1600px;
  background: #f5f6fb;
  text-align: center;
}

h2 {
  color: #3652A1;
  margin: 20px;
}

span {
  color: #3652A1;
}

.el-button {
  width: 320px;
}

.content .content-wrapper {
  width: 1200px;
  /*height: 500px;*/
  background: #FFFFFF;
}

.el-row {
  margin-bottom: 20px;
  &:last-child {
    margin-bottom: 0;
  }
}















































