							height: 38px;
							border: 1px solid #387E38;
							border-radius: 2px;
						}
					}
					.wc-content{
						float: left;
						width: 1540px;
						border: 1px solid #E8E6F3;
						background: #FFFFFF;
						
						/*left: 20px;*/
					}
