<template>
  <div id="wzdinwei" v-show="!show">
    <div class="box">
    <div class="zb">
      <span class="cl"><b>车辆定位</b></span>
     <a href="#"><span class="el-icon-close" @click="show=!show"></span></a> 
    </div>
    <div class="boq">
      <div class="yi">
      	<!--王政的图-->
        <!--<img src="../../assets/tu.png" alt="">-->
        <span>
          <b><p>东风量产</p></b>
          <p>车牌号码:宁夏B:110z</p>
          <p>联系方式:138xxxx7552</p>
        </span>
      </div>
    <div class="chel">
      <p>车辆类型:大型装载车辆</p>
      <p>车辆长度:长:26米，高:12米</p>
      <p>身份证号:622827xxxxxxxxxx2915</p>
    </div>
   <div style="width:660px;height:320px;border:#ccc solid 1px;" id="dituContent"></div>


    </div>
    </div>
  </div>

</template>

<script>
   export default {
     data () {
    return {
        show:true,
      msg: 'Welcome to Your Vue.js App'
      }
   },
      mounted(){
        function initMap(){
        createMap();//创建地图
        setMapEvent();//设置地图事件
        addMapControl();//向地图添加控件
    }
    
    //创建地图函数：
    function createMap(){
        var map = new BMap.Map("dituContent");//在百度地图容器中创建一个地图
        var point = new BMap.Point(116.170937,40.182534);//定义一个中心点坐标
        map.centerAndZoom(point,17);//设定地图的中心点和坐标并将地图显示在地图容器中
        window.map = map;//将map变量存储在全局
    }
    
    //地图事件设置函数：
    function setMapEvent(){
        map.enableDragging();//启用地图拖拽事件，默认启用(可不写)
        map.enableScrollWheelZoom();//启用地图滚轮放大缩小
        map.enableDoubleClickZoom();//启用鼠标双击放大，默认启用(可不写)
        map.enableKeyboard();//启用键盘上下左右键移动地图
    }
    
    //地图控件添加函数：
    function addMapControl(){
        //向地图中添加缩放控件
	var ctrl_nav = new BMap.NavigationControl({anchor:BMAP_ANCHOR_TOP_LEFT,type:BMAP_NAVIGATION_CONTROL_LARGE});
	map.addControl(ctrl_nav);
        //向地图中添加缩略图控件
	var ctrl_ove = new BMap.OverviewMapControl({anchor:BMAP_ANCHOR_BOTTOM_RIGHT,isOpen:1});
	map.addControl(ctrl_ove);
        //向地图中添加比例尺控件
	var ctrl_sca = new BMap.ScaleControl({anchor:BMAP_ANCHOR_BOTTOM_LEFT});
	map.addControl(ctrl_sca);
    }
    
    
    initMap();//创建和初始化地图
      }
   }
</script>

<style lang='scss' scoped>
*{
  margin:0;
  padding: 0;
}
.iw_poi_title {color:#CC5522;font-size:14px;font-weight:bold;overflow:hidden;padding-right:13px;white-space:nowrap}
    .iw_poi_content {font:12px arial,sans-serif;overflow:visible;padding-top:4px;white-space:-moz-pre-wrap;word-wrap:break-word}
  #wzdinwei{
  width:100%;
  height:1080px; 
  background:rgba(0, 0, 0,0.5);
  padding-top:190px;
  .box{
    width:704px;
    height:690px;
    background:#fff; 
    margin: 0 auto;
    .zb{
      width:100%;
      height:48px;
      border-bottom:1px solid #aaaaaa;
      line-height:48px;
      .cl{
        float:left;
        margin-left:15px;
      }
      .el-icon-close{
        line-height:48px;
        margin-right:15px;
        float:right;
        font-size:12px;
      }
    }
    .boq{
      width:660px;
      margin:0 auto;
    }
    .yi{
      height:100px;
      border-bottom:2px solid #aaaaaa;
      padding: 20px;
      img{
        float:left;
        width: 120px;
        height:80px;
      }
      span{
        float:left;
        margin-left:20px;
        
      }
      
    }
    .chel{
      float:left;
      margin-top:28px;
      margin-bottom:28px;
    }
   
  }
    &:after {
    display: block;
    clear: both;
    content: "";
}

  }


</style>



