          <div class="txhead">
           <span>提现</span>
          </div>
        <!--步骤条-->
            <el-steps :space="200" :active="0.5" center=true>
            <el-step title="填写银行卡/金额"></el-step>
            <el-step title="确认提现信息"></el-step>
            <el-step title="申请提现成功"></el-step>
            </el-steps><!--步骤条结束-->
            <!--提示信息-->
         <p class="tishi">
           <i class="el-icon-information"></i>
            可提现金额25.64元，充值金额0.00元。充值金额不许提现，如需返回，请点击“
            <span>发起充值退回</span>”
         </p>
            <!--添加银行卡-->
            
            <el-upload
                class="upload-demo"
                action="https://jsonplaceholder.typicode.com/posts/"
                :on-change="handleChange"
                :file-list="fileList3">
