            width:100px;
             display:inline-block;
             text-align:right;
         }
         color:#e5e2e2;
         .el-input{
             width:210px;
             display:inline-block;
         }
         .money-info{
             margin-left:200px;
             margin-top:24px;
             color:#babbba;
             a{
                 color:#5493cd;
                 cursor:pointer;
             }
         }
     }
     .next{
         margin:24px 0 24px 200px;
         
             background:red;
             color:white;
     }
      
 }
</style>



