</template>
<script>
	export default {}
</script>
<style lang="scss" scoped>
	.wc-l{
		.wc-bottom{
			overflow: hidden;
			margin: 0 auto;
			width: 1280px;
			height: 720px;
			border: 1px solid #E8E6F3;
			
			position: relative;
			.po-message{
				position: absolute;
				bottom:27px ;
				right:30px ;
				width: 280px;
				height: 225px;
				background: #FFFFFF;
				.wc-cont-t{
					width: 240px;
					margin: 0 auto;
					font-size: 12px;
					color: #6B6B6B;
				}
				.wc-cont-b{
					padding-top: 30px;
					width: 240px;
					margin: 0 auto;
					font-size: 12px;
					color: #4C4C4C;
					line-height: 30px;
					i{
						margin-right: 10px;
						
					}
				}
			}
		}
	}
</style>


