<template>
  <!--<el-button icon="check" type="text" @click="open4">还款</el-button>-->
  <el-button type="primary" icon="check" class="hk" @click="open4">还款</el-button>
</template>
<script>
  export default {
    methods: {
      open4() {
        const h = this.$createElement;
        this.$msgbox({
          title: '请输入支付密码',
          message: h('div',null,[
            h('input', { style: 'background-color:#F5F6FB;border:1px solid #C4C4C4;width:294px;height:40px;' })
          ]),
          showCancelButton: true,
          confirmButtonText: '取消',
          cancelButtonText: '确定',
          beforeClose: (action, instance, done) => {
            if (action === 'confirm') {
              instance.confirmButtonLoading = true;
              instance.confirmButtonText = '执行中...';
              setTimeout(() => {
                done();
                setTimeout(() => {
                  instance.confirmButtonLoading = false;
                }, 300);
              }, 3000);
            } else {
              done();
            }
          }
        }).then(action => {
          this.$message({
            type: 'info',
            message: 'action: ' + action
          });
        });
      },
    }
  }
</script>
<style lang="scss" scoped>
	.el-message-box__wrapper{
		.el-message-box{
			width:700px;
			height:210px;
		}
	}
	.el-message-box__content{
		margin-top: 10px;
		border-top: 1px solid #F5F6FB;
		text-align: center;
	}
	.el-message-box__btns{
		background:#F5F6FB ;
	}
</style>


