<template>
    <div class="mz-dh clearfix">
        <div class="mz-dh-img">
            <img src="../../assets/ioo.png" alt=""/>
        </div>
        <div class="mz-dh-nav">
            <ul class="clearfix">
                <router-link tag="li" active-class="active" to="/Syindex"><span>首页</span></router-link>
                <router-link tag="li" active-class="active" to="/Motorcade"><span>车队</span></router-link>
                <router-link tag="li" active-class="active" to="/Mymoto8/mooto"><span>运单</span></router-link>
                <router-link tag="li" active-class="active" to="/Mymoto9/fukuans"><span>支付</span></router-link>
                <router-link tag="li" active-class="active" to="/Mymoto10/fapiao1"><span>发票</span></router-link>
                <router-link tag="li" active-class="active" to="/Mymoto11/wzbaoli"><span>保理</span></router-link>
                <router-link tag="li" active-class="active" to="/Mymoto12/Hellow"><span>账户</span></router-link>
                <router-link tag="li" active-class="active" to="/Mymoto18/Deposits"><span>设置</span></router-link>
                <router-link tag="li" active-class="active" to="/Mymoto19/Xxhz"><span>消息</span></router-link>
            </ul>

        </div>
        <div class="mz-dh-dl">
            <span>登录</span>
            <span>注册</span>
        </div>
        <div class="">
        	<router-view></router-view>
        </div>
    </div>
</template>
<script>
export default {

}
</script>
<style lang="less" scoped>
*{
    margin: 0;
    padding: 0;
}
.mz-dh{
    width: 1600px;
    height: 80px;
    border-bottom: 1px solid #e6e9f2;
    .mz-dh-img{
        height: 80px;
        width: 200px;
        text-align: center;
        float: left;
        img{
            width: 130px;
            height: 80px;
        }
    }
    .mz-dh-nav{
        width: 100px;
        height: 80px;
        line-height: 80px;
        float: left;
        ul{
            width: 1300px;
            height: 60px;
            li{
                float: left;
                padding: 0 30px;
                font-size: 20px;
                span{
                    display: inline-block;
                    height: 76px;
                }
                &.active{
                    span{
                        color: #3052ac;
                        cursor:pointer;
                        border-bottom: 4px solid #3052ac;
                    }

                }
            }
        }
    }
    .mz-dh-dl{
        width: 100px;
        height: 80px;
        line-height: 80px;
        float: right;
        span{
            margin-left: 10px;
            cursor:pointer;
        }
    }
}
.clearfix:after{
    clear: both;
    content: '';
    display: block;
}
</style>



