<template lang="html">
  <div class="">
    <!--内容-->
        <div class="container">
          <div class="txhead">
           <span>提现</span>
          </div>
        <!--步骤条-->
            <el-steps :space="200" :active="2.5" center=true>
            <el-step title="填写银行卡/金额"></el-step>
            <el-step title="确认提现信息"></el-step>
            <el-step title="申请提现成功"></el-step>
            </el-steps><!--步骤条结束-->
         
  <!--表单验证-->
        <el-form :model="ruleForm"  ref="ruleForm" label-width="150px" class="demo-ruleForm">
          <el-form-item label="真实开户人姓名">
                <input type="text" class="kun">
          </el-form-item>
           <el-form-item label="选择银行卡：">
                <el-input v-model="form.bank"></el-input>
           </el-form-item>
           <el-form-item label="提现金额：">
                <span class="jine">10:00元</span>
          </el-form-item>
          <el-form-item label="支付密码：">
                <el-input type="password"></el-input><a class="a">忘记密码?</a>
           </el-form-item>
        </el-form><!--表单验证结束-->
        <router-link to="/Naviga/Motorcade3/Tixian5" class="el-button next" tag="button">
             确认提现</router-link><a class="a">返回修改</a>
        </div> <!--内容结束-->
  </div>
</template>

<script>
export default {
  components:{
  },
  data(){
      return{
          form:{
              bank:"招商银行"
          }
      }
  }

}
</script>

<style lang="less" scoped>
   .container{
      font-size:12px;
      position:absolute;
      left:230px;
      width:790px;
      margin-top:20px;
      padding:20px;
      border:1px solid #e7e8ed;
     .txhead{
         border-bottom:1px solid #d0d0d0;
         padding-bottom:10px;
         margin-bottom: 20px;
         span{
             font-size:16px;
             font-weight:bolder;
             
         }
     }
     .el-steps{
         margin:50px auto;
     }
     .el-form {
         .kun{
             width:80px;
             height:26px;
             background:#4cacc6;
             border:3px solid #28768a;
             border-radius:10px;
         }
         .el-input{
             width:220px;
         }
         .jine{
             color:#fcb20a;
             font-weight:bolder;
         }
         
     }
     .a{
             color:#6389d1;
             margin-left:20px;
             cursor:pointer;
         }
      .next{
         margin:24px 0 24px 200px;
         
             background:red;
             color:white;
     }
 }
</style>



