<template>
    <div class="mz-zf">
        <el-row class="mz-zf-top">
            <el-col :span="10" class="mz-zf-top-left"><h2 class="mz-zf-h2">资费说明</h2><div>/&ensp;说明说明说明说明说明说明</div></el-col>
            <el-col :span="14" class="mz-zf-top-rigth"><div>返回上一页</div><i class="icon iconfont icon-jiantou1"></i></el-col>
        </el-row>
        <el-row class="mz-zf-bottom">
            <el-col :span="24">
                <el-table
                :data="tableData"
                border
                style="width: 100%"
                class="mz-tab">
                <el-table-column
                    prop="date"
                    label="服务项目"
                    width="360">
                </el-table-column>
                <el-table-column
                    prop="name"
                    label="收费标准"
                    width="360">
                </el-table-column>
                <el-table-column
                    prop="address"
                    label="备注说明">
                </el-table-column>
                </el-table>
            </el-col>
        </el-row>
    </div>
</template>
<script>
export default {
    data() {
        return {
            tableData: [{
                date: '手机定位',
                name: '0.2元/次',
                address: '定位失败不扣费'
            }, {
                date: '手机短信',
                name: '0.1元/次',
                address: '发送失败不扣费'
            }, {
                date: '身份认证',
                name: '5元/次',
                address: '操作失败不扣费'
            }, {
                date: '货运保险',
                name: '费率千分之一',
                address: '平安运输损失险-普通货物-综合险资费标准'
            }]
        }
    }
}
</script>
<style lang="less" scoped>
*{
    margin: 0;
    padding: 0;
}
.mz-zf{
    background: #fafbff;
    padding: 0 28px;
    width:1600px;
    height: 1294px;
}
.mz-zf-top{
    width: 1600px;
    height: 90px;
    line-height: 90px;
    .mz-zf-h2{
        padding-right: 20px;
        font-size: 24px;
        color: #3454a1;
    }
    .mz-zf-top-left{
        display: flex;
        font-size: 14px;
        color: #8894ac;
    }
    .mz-zf-top-rigth{
        display: flex;
        justify-content: flex-end;
        color: #9a9b9f;
    }
}
.mz-zf-bottom{
    width: 100%;
    background: #FFF;
    border:1px solid #e7e8ed;
    padding: 30px;
}
</style>



